@import url('../components/personal-plan.css');


.header{
  background: #0B0B0B;
}

.title-main{
  margin-bottom: 30px;
  margin-top: 70px;
  @media (max-width: 767.98px) {
    margin-top: 35px;
  }
  .subtitle {
    p {
      margin-bottom: 3px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
    }
  }
  h1{
    color: #0B0B0B;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    @media (max-width: 767.98px) {
      font-size: 30px;
      font-weight: 600;
      line-height: normal;
    }
  }
}


.about {
  background: #F6F6F8;
  padding-top: 110px;
  @media (max-width: 767.98px) {
    padding-top: 74px;
  }
  &__list{
    display: flex;
  }
  &__col{
    width: 50%;
    margin: 0 auto;
  }
  &__item{
    width: 436px;
  }
  &__photo{
    width: 364px;
    height: 436px;
    img{
      border-radius: 6px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__name{

  }
}